import React , { Component } from 'react';


class Stript extends Component{
    marginL(){
        return (parseFloat(window.innerWidth/4.5));
    };

    marginT(){
        return (parseFloat(window.innerHeight*0.3));
    };
    render(){
        let left = this.marginL();
        let top = this.marginT();
        let _width = parseFloat(window.innerWidth);
        let _height = parseFloat(window.innerHeight/1.1);
        return (
            <div className="stript" style={{width:_width,height:_height}}>  
                <div style={{marginLeft:left,marginTop:top,position:'absolute'}}>
                      <h style={{fontSize:150,color:'white'}}><strong>SafeTrip</strong></h>
                      <h2 style={{fontSize:40,color:'white',textShadow:"6px 6px 6px black"}}>A Real-Time Cyber Security suite for IoT/OT connected devices </h2>
                </div>
                
            </div>
        );
    };
}


export default Stript;