import React , { Component } from 'react';
import SafeTrip from './Components/SafeTrip/safeTrip';
import SafeCAN from './Components/SafeCAN/safeCAN';
import SafeGaurd from './Components/SafeGaurd/safeGaurd';
import SecurityServ from '/home/daniel/ReactExpress/products/src/Components/securityServises/security_serv.jsx';

class App extends Component{
   state={
     file1:"",
     file2:"",
     file3:""
   };

  render(){
    if(window.location.href === "https://localhost:3000/SafeTrip/")
    {
      return(
        <SafeTrip file={this.state.file1}/>
      );  
    }
    else if(window.location.href === "https://localhost:3000/SafeCAN/")
    {
      return(
      <SafeCAN file={this.state.file2} />
      );
    }
    else if(window.location.href === "https://localhost:3000/SafeGaurd/")
    {
      return(
          <SafeGaurd file={this.state.file3}/>
       );
    }
    else if(window.location.href === "https://localhost:3000/Security%20&%20services/"){
      return(
        <SecurityServ file={this.state.file1}/>
      );
    }
    return <SafeTrip/>;
  };
} 

export default App;
